import styled from "styled-components";
import { Link } from "react-router-dom";
import { Button } from "../../atoms/main/button";
import Instagram from "../../../assets/images/main/Instagram.png"
import {ReactComponent as NewPost } from "../../../assets/images/insta/NewPost.svg"

const Portfolio2 = ()=>{
    return <>
    <Wrapper>
        <Left>
    <ImagePortfolio src={Instagram}/>  
    <Link to="/insta/login" target="_blank" rel="noopener noreferrer">
    <Button>View</Button>
    </Link>
    </Left>
    <Right>
        <Header>  </Header>
        <Content>
        
        <b> </b>: React.js<br/>
        <p>create-react-app</p> 
        <p>styled-components</p>
        <p>react-router-dom</p>
        <b> </b>:
        <ul>       
        <li>        carousel .<br/> 
          setInterval   4   index    ,  
         useEffect       cleanup .<br/> 
        </li>
        <li>nav      ,  nav   .<br/> 
          nav   , react router dom Outlet .<br/> 
        </li>
        <li> nav    <NewPost/> , modal       .<br/> 
                 body   overflow = "hidden" , 
        modal     .<br/> 
        </li>
        <li>   ,            .<br/> 
          file  input  onChange           ,
         FileReader   readAsDataURL()   , onload        .  
        </li>
        <li>          .<br/> 
          sidebar position : sticky  top: 120px static  120px fixed  .<br/> 
        </li>
        </ul>
        </Content>
        
   
    </Right>  
    </Wrapper>
    </>
}
const Wrapper = styled.div`
display:flex;
margin: 30px 70px;
`
const Left = styled.div`
display: flex;
flex-direction: column;
align-items: center;
`
const Right = styled.div``
const Header = styled.h2``
const ImagePortfolio = styled.img`
width: 500px; 
height:400px;
border:1px solid #ddd;
border-radius:10px;
margin-right: 20px;
margin-bottom: 50px;
`
const Content = styled.div`
font-size: 17px;
line-height: 1.8;
p{
    text-indent: 80px;
    margin:0;
}
ul{
    margin:0;
}
`

export default Portfolio2;