import styled from "styled-components";
import { Link } from "react-router-dom"
import { Button } from "../../atoms/main/button";
import youtube from "../../../assets/images/main/youtube.png"

const Portfolio1 = ()=>{
    return <>
   <Wrapper>
      <Left>
        <ImagePortfolio src={youtube}/>  
        <Link to="/youtube" target="_blank" rel="noopener noreferrer">
          <Button>View</Button>
        </Link>     
      </Left>
      <Right>
        <Header>  </Header>
        <Content>
        <b>  :</b> React.js<br/>
        <p>create-react-app</p> 
        <p>styled-components</p>
        {/* ( ES6   props   ) */}
        <p>react-router-dom</p>
        {/* (  URL   URL       ) */}
        <b> </b>: 
        <ul>
        <li>dropdown   body   dropdown   .<br/>
         useRef  dropdown       dropdown    
         document.body.addEventListener   custom hook ,  dropdown  .
        </li>
        <li>
              url          .<br/>
          url parameter      (Dynamic Routing) , 
         useParams         iframe        .
             mock data  find()          (, ,   )    .  
        </li>
        </ul>
        </Content>
       
      </Right>
    </Wrapper>
    </>
}
const Wrapper = styled.div`
display:flex;
margin: 30px 70px;
`
const Left = styled.div`
display: flex;
flex-direction: column;
align-items: center;
`
const Right = styled.div``
const Header = styled.h2``
const ImagePortfolio = styled.img`
width: 500px; 
height:400px;
border:1px solid #ddd;
border-radius:10px;
margin-right: 20px;
margin-bottom: 50px;
`
const Content = styled.div`
font-size: 17px;
line-height: 1.8;
p{
    text-indent: 80px;
    margin:0;
}
ul{
    margin:0;
}
`
export default Portfolio1;